<template>
  <div class="box">
    <div id="Player"></div>
  </div>
</template>
<script lang="ts" setup>
  import { onMounted, ref } from 'vue';
  import { getVideoInfoUrl } from './data';
  import * as FileApi from '@/api/infra/file';

  const player = ref();

  const initWasmPlayer = () => {
    player.value = new WasmPlayer(null, 'Player', callbackFun, {
      cbUserPtr: this,
      decodeType: 'auto',
      live: true,
      openAudio: 1,
      closeAudio: 0,
      BigPlay: false,
      Height: true,
    });
    // 调用播放
    const url = FileApi.attachmentDownloadUrl(getVideoInfoUrl);
    console.log(1, url);
    player.value.play(getVideoInfoUrl, 1);
  };

  const callbackFun = (e) => {
    console.log('callbackFun', e);
  };

  onMounted(() => {
    initWasmPlayer();
  });
</script>
<style lang="less" scoped>
  .box {
    width: 100%;
    height: 100%;
  }
</style>
